import request from "@/utils/request";
import { ouchnCustom } from "@/service";

//
export function teachingAttrList(dictType) {
  return request({
    url: ouchnCustom + "/web/goods/getDictListByDictType?dictType=" + dictType,
    method: "get"
  });
}

//
export function TeachingClassList(catalogType) {
  return request({
    url:
      ouchnCustom + "/web/goods/getTreeSelectByType?catalogType=" + catalogType,
    method: "get"
  });
}
//
export function goodsList(data) {
  return request({
    url: ouchnCustom + "/web/goods/selGoodsList",
    method: "post",
    data: data
  });
}
//
export function goodsInfoDetail(goodsId) {
  return request({
    url: ouchnCustom + "/web/goods/selGoodsById?goodsId=" + goodsId,
    method: "get"
  });
}
// 
export function recommendGoods(goodsId) {
  return request({
    url: ouchnCustom + "/web/goods/getRecomGoodsList?goodsId=" + goodsId,
    method: "get"
  });
}
// 
export function recommendSubject(goodsId) {
  return request({
    url: ouchnCustom + "/web/goods/getRecomSubjectList?goodsId=" + goodsId,
    method: "get"
  });
}
// 
export function Listevaluate(query) {
  return request({
    url: ouchnCustom + "/web/goods/getEvaluateListByGoodsId",
    method: "get",
    params: query
  });
}
// 
export function CartAdd(goodsId, userId) {
  return request({
    url:
      ouchnCustom +
      "/web/api/ocShoppingCart/addShoppingCart?goodsId=" +
      goodsId +
      "&userId=" +
      userId,
    method: "get"
  });
}
//
export function IsCartAdd(goodsId) {
  return request({
    url:
      ouchnCustom +
      "/web/api/ocShoppingCart/isAddShoppingCart?goodsId=" +
      goodsId,
    method: "get"
  });
}
//
export function BookshelfAdd(query) {
  return request({
    url: ouchnCustom + "/web/goods/addBookshelf",
    method: "get",
    params: query
  });
}
//
export function IsBookshelfAdd(query) {
  return request({
    url: ouchnCustom + "/web/goods/isAddBookshelf",
    method: "get",
    params: query
  });
}
//
export function delBookshelf(query) {
  return request({
    url: ouchnCustom + "/web/goods/delBookshelf",
    method: "get",
    params: query
  });
}
//
export function reportComment(data) {
  return request({
    url: ouchnCustom + "/web/api/ocEvaluate/addReport",
    method: "post",
    data: data
  });
}

//
export function checkIsBuy(params) {
  return request({
    url: ouchnCustom + "/web/read/selectIsBuy",
    method: "get",
    params
  });
}
//
export function SelGoodsStatus(goodsId) {
  return request({
    url:
      ouchnCustom + "/web/api/ocShoppingCart/selGoodsStatus?goodsId=" + goodsId,
    method: "get"
  });
}
//
export function SelSubjectStatus(id) {
  return request({
    url: ouchnCustom + "/web/api/ocShoppingCart/selSubjectStatus?id=" + id,
    method: "get"
  });
}
//
export function isHavingPermissions(data) {
  return request({
    url: ouchnCustom + "/web/read/isHavingPermissions",
    method: "POST",
    data: data
  });
}

//
export function updateRecordAndSchedule(data) {
  return request({
    url: ouchnCustom + "/web/otherRead/updateRecordAndSchedule",
    method: "POST",
    data: data
  });
}

//
export function selectReadSchedule(data) {
  return request({
    url:
      ouchnCustom +
      "/web/otherRead/selectReadSchedule?goodsFileId=" +
      data.goodsFileId +
      "&platformCode=" +
      data.platformCode +
      "&stuNum=" +
      data.stuNum,
    method: "GET"
    // data: data
  });
}
